.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

button.link {
  border: 1px solid white;
  padding: 5px;
  color: inherit;
  background: inherit;
  font-family: inherit;
  font-size: inherit;
}

.nav {
  list-style: none;
  padding: 0;
  margin: 0 0 10px 0;
  line-height: 38px;
  border-bottom: white solid 1px;
}

.nav>li {
  display: inline-block;
}

.nav>li>a {
  color: white;
  text-decoration: none;
  padding: 10px;
}

.nav>li>a.active {
  background: white;
  color: black;
  border: white solid 1px;
}

.container {
  margin: 0 auto;
  max-width: 1100px;
}

.log-link {
  float: right;
  position: relative;
}

.log-link .profile-pic {
  position: absolute;
  bottom: 5px;
  left: -60px;
}

.turn {
  display: inline-block;
  width: 400px;
  margin-bottom: 20px;
}

.board {
  width: 264px;
  padding: 20px;
  background: white;
  display: inline-block;
}

.points {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.point {
  display: inline-block;
  width: 7.7%;
  height: 150px;
  position: relative;
  border-top: 1px black solid;
  border-radius: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.point.selected .checker:last-child{
  opacity: .3;
}

.point.possible{
  background-color: rgba(0, 152, 0, 0.16);
}

.point:nth-child(1){
  border-right: solid 1px black;
  -ms-flex-order: 12;
      order: 12;
}

.point:nth-child(2){
  -ms-flex-order: 11;
      order: 11;
}

.point:nth-child(3){
  -ms-flex-order: 10;
      order: 10;
}

.point:nth-child(4){
  -ms-flex-order: 9;
      order: 9;
}

.point:nth-child(5){
  -ms-flex-order: 8;
      order: 8;
}

.point:nth-child(6){
  -ms-flex-order: 7;
      order: 7;
  border-left: solid 1px black;
  margin-left: 7.6%;
}

.point:nth-child(7){
  border-right: solid 1px black;
  -ms-flex-order: 6;
      order: 6;
}

.point:nth-child(8){
  -ms-flex-order: 5;
      order: 5;
}

.point:nth-child(9){
  -ms-flex-order: 4;
      order: 4;
}

.point:nth-child(10){
  -ms-flex-order: 3;
      order: 3;
}

.point:nth-child(11){
  -ms-flex-order: 2;
      order: 2;
}

.point:nth-child(12){
  border-left: solid 1px black;
  -ms-flex-order: 1;
      order: 1;
}

.point:nth-child(13){
  border-left: solid 1px black;
}

.point:nth-child(24){
  border-right: solid 1px black;
}

.point:nth-child(18){
  border-right: solid 1px black;
  margin-right: 7.6%;
}

.point:nth-child(19){
  border-left: solid 1px black;
}

.point:nth-child(-n+12) {
  border-top: 0;
  border-bottom: 1px black solid;
}

.point:nth-child(-n+12) .checkers{
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom: 0;
}

.checkers {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
}

.checker {
  border: 1px solid black;
  border-radius: 50%;
  height: 18px;
  width: 18px;
}

.checker.b {
  background: black;
}

.dice {
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
  height: 80px;
}

.die {
  display: inline-block;
  margin-right: 10px;
  width: 75px;
  height: 75px;
  background: url('../img/dice-faces.svg');
}

.die.number-undefined {
  background: none;
}

.die.number-1 {
  background-position: -43px 129px;
}

.die.number-2 {
  background-position: -122px 129px;
}

.die.number-3 {
  background-position: -201px 129px;
}

.die.number-4 {
  background-position: -280px 129px;
}

.die.number-5 {
  background-position: -359px 129px;
}

.die.number-6 {
  background-position: -438px 129px;
}

.hit {
  background-color: white;
  position: relative;
  left: -162px;
  top: 30px;
}

.turn, .board, .hit {
  float: left;
}

.cries {
  list-style: none;
  padding: 0;
  margin: 0;
}

.cry {
  border: 1px solid white;
  margin-bottom: 5px;
}

.cry .cry-body,
.cry .cry-collapse {
  padding: 10px;
  display: inline-block;
}

.cry .cry-collapse { border-right: 1px white solid; }

.cry.error {
  color: red;
}

.cry.warning {
  color: orange;
}

.cry.success {
  color: green;
}

.cry.info {
  color: white;
}

.editable-field {
  margin-bottom: 20px;
  display: inline-block;
}

.editable-field .inactive-field {
  cursor: pointer;
}

.editable-field .inactive-field .edit {
  display: none;
  color: #aaa;
  margin-left: 10px;
  font-size: smaller;
  font-style: italic;
}

.editable-field .inactive-field:hover .edit {
  display: inline-block;
}

.editable-field .active-field .cancel-field,
.editable-field .active-field .save-field {
  margin-left: 10px;
}



